//grid
$max-width: 650px;
$col-gutter: 30px;
$break-large: 400px;

.clearfix { display:table; 
            line-height:0; 
            content:""; 
            clear:both;
}
.row {
  max-width:$max-width + $col-gutter;
  margin:0 auto;
  .row {
    margin-left:-$col-gutter; 
    margin-right:-$col-gutter;
  }
  .col { 
    padding-left:$col-gutter; 
    padding-right:$col-gutter;
  }
}

//large grid classes
@media (min-width:$break-large) {
  .hide-lrg { display:none; }
  @for $i from 1 through 12 {
    .lrg-#{$i} { width:$i/12*100%; float:left; }
  }
}

//bbc slider
.slider-wrap {
  .slider { 
    height:330px; 
    position:relative;
  }
  .outer {
    position:relative; 
  }
  .outer, .tray, section {
    height:100%; 
  }
  .tray, section { 
    position:absolute; 
    width:100%; 
    height:100%;
  }
  section { 
    overflow:hidden;
    padding-left: 60px;
    padding-right: 60px;
    img {
      width:100%; 
      max-width:100%; 
      margin: 0 auto;
      display: block;
      margin-top: 0px !important;
      height: 320px;
    }
  }
  .arrow {
    position:absolute; 
    top:50%;
    margin-top:-30px;
    height:60px;
    width:60px;
    z-index:1; 
    cursor:pointer; 
    z-index:15; 
    background-color:black; 
    &.left {
      right:-30px; 
      background-image:url(http://www.garethweaver.com/codepen/images/bbc-left.png);
      span {
        left:60px;
        padding:0 20px 0 0; 
      }
    }
    &.right {
      left:-30px; 
      background-image:url(http://www.garethweaver.com/codepen/images/bbc-right.png);
      span {
        right:60px; 
        padding:0 0 0 20px;  
      }
    }
    span { 
      height:60px;
      line-height:60px;
      position:absolute;
      display:none; 
      color:#fff;
    }
    &:hover {
      /*background-color:$blue;*/
      span {
        display:block; 
      }
    }
  }
  .mask {
    display:block;
    position:absolute;
    top:0; 
    height:100%;
    width:200%; 
    /*background:rgba(#fff,0.7);*/
    z-index:10; 
    cursor: ew-resize;
    &.left { 
      right:100%; 
    }
    &.right { 
      left:100%;
    }
  }
  .slider-nav { 
    position:relative;
    font-size:14px;
    text-align:center;
    margin:15px 0 0; 
    overflow:hidden;
    ul { 
      position:relative;
      margin:0; 
      padding:0; 
    }
    .dots{
      width: 100%;
      border-bottom: 1px dashed #fff;
      position: absolute;
      top: 11px;
    }
    li {
      list-style: none;
      float:left;
      width:25%; 
      box-sizing:border-box;
      height: 24px;
      position: relative;
      a {
        @include transition(color 0.2s ease-in-out);
        /*        height:60px;
                line-height:60px;*/
        /*border-top:4px solid ;*/ 
        height: 20px;
        
        display:block; 
        font-weight:bold; 
        margin:0 4px;
        span{
          display: none;
          padding: 0 15px
        }
      }
    }
    .current{
      a{
        background-color: #009046;
        border-right: 10px solid #363636;
        text-align: left;
        span{
          display: block;
          color: #fff;
        }
      }
    }
    .highlight { 
      /*      position:absolute;
            margin:0 4px; 
            top:0; 
            left:0;
            width:24%;
            box-sizing:border-box;
            border-top:4px solid; */
    }
  }
}

